	module.exports = {"data":{"markdownRemark":{"html":"<p align=\"center\">\n  <img style='width:60%;' border=\"0\" alt=\"testo sopra\" src=\"/media/photos/ruota-wendy-andrew.jpg\">\n</p>\n<p>La Ruota dell'Anno è molto più di un calendario che segna il passaggio delle stagioni, è metafora dell'intera esistenza, è una bussola che guida al riconoscimento di una sacralità dimenticata e al ri-allineamento con essa: quella che è nei nostri corpi, nelle nostre idee, nelle nostre esperienze, nelle nostre azioni, nella fantastica natura in cui viviamo, nel nostro essere parte dell'Universo. Qui. Ora.</p>\n<p>Il percorso è composto da 8 incontri che si svolgono nell'arco di un anno, da autunno ad autunno, in prossimità di 8 antiche feste sacre i cui echi sono giunti attraverso il tempo fino a oggi. Ciascun incontro sarà dedicato a un aspetto della Grande Dea nella sequenza in cui è celebrata a Glastonbury/Avalon: inizieremo a conoscere come la sua energia può essere sperimentata nella vita e nella quotidianità, da ciascuna/o di noi, senza intermediari.<br>\nCelebreremo in questa successione: l'archetipo della Vecchia Crona, la Signora dell'Aria, l'archetipo della Fanciulla, la Signora del Fuoco, l'archetipo dell'Amante, la Signora dell'Acqua, l'archetipo della Madre, la Signora della Terra.<br>\nL'ultimo incontro si svolgerà in un weekend residenziale e prevede una personale cerimonia di dedicazione (facoltativa) alla Grande Madre.</p>\n<p><strong>Il per.corso è aperto a donne e uomini.</strong></p>\n<br/>\n<p><strong>Informazioni:</strong><br>\nSono previsti momenti di studio individuale (preparatori agli incontri), momenti di confronto e scambio, momenti di attività esperienziali, momenti di celebrazione. Creeremo oggetti sacri, suoneremo, canteremo, viaggeremo in meditazione, danzeremo, condivideremo risate e lacrime, biscotti e tisane.<br>\nRi-membreremo e ri-celebreremo alcuni archetipi del Divino Femminile, noteremo quali nomi sono stati associati a questi archetipi, riferendoci sia alla Ruota di Brigit-Ana (utilizzata presso il Tempio della Dea di Glastonbury) sia alle Dee e agli aspetti della Grande Madre che sono rilevabili nelle leggende del nostro territorio.<br>\n<strong>La frequenza è obbligatoria.</strong><br>\nÈ richiesta la presenza agli incontri in quanto il corso si sviluppa secondo un andamento progressivo, perdere una lezione significa perdere una parte integrante dell'insieme e la presenza è propedeutica anche alla creazione di un gruppo di lavoro e di un Cerchio di Pratica.<br>\nPer la parte teorica il testo di riferimento è \"Sacerdotessa di Avalon, Sacerdotessa della Dea\" di Kathy Jones, Ester Edizioni (costo e reperimento a carico del/della partecipante).<br>\nLa facilitatrice fornirà:  </p>\n<ul>\n<li>Dispense, articoli o altro materiale teorico</li>\n<li>Una breve lista di testi consigliati</li>\n<li>Materiale di massima per attività creative, occorrente per le celebrazioni e varie attività</li>\n<li>Indicazioni su oggetti a tema o materiale che i partecipanti dovranno portare (fornite di volta in volta, prima di ogni incontro)</li>\n</ul>\n<br/>\n<p><strong>Costi:</strong><br>\nUn piccolo preambolo: nel nostro paese c'è molta resistenza rispetto alla possibilità che un percorso che si presenta legato a qualche forma di spiritualità possa prevedere dei costi a carico dei partecipanti.<br>\nÈ però importante tenere conto che ci sono dei costi di organizzazione, a volte anche importanti (affitto della sala, trasporti, acquisto materiali per attività, acquisto materiali per le celebrazioni, cibo e bevande messe a disposizione, volantini, tempo di progettazione).<br>\nIn un mondo che domanda di essere pagato in denaro, non è possibile proporre gratuità senza che qualcuno (l'organizzazione) ne perda.<br>\nInoltre, come Associazione di Promozione Sociale <em>Tempio della Grande Madre</em>, i corsi e per.corsi rappresentano il nostro modo principale per sostenerci: tutto il denaro ricavato verrà reinvestito in nuovi progetti connessi con gli scopi associativi. Il nostro obiettivo più grande è pervenire a una sede stabile da adibire a moderno Tempio della Grande Madre dove custodire la Fiamma della Grande Madre delle Dolomiti, riaccesa il 20 settembre 2015.<br>\n(Per saperne di più e capire meglio la nostra posizione puoi leggere l'articolo <a href=\"/spiritualità-e-denaro\" target=\"_blank_\">\"Spiritualità e denaro\"</a>)</p>\n<p><strong>Il costo per l'anno 2018/2019 è di € 400 + tessera associativa di € 15.</strong><br>\nSi considerano esclulsi da questa cifra l'acquisto del testo di Kathy Jones, le spese extra (pranzi all'esterno, eventuali pernottamenti) e il materiale da portare a ciascun incontro.</p>\n<p><strong>LE ISCRIZIONI SONO OBBLIGATORIE ENTRO VENERDÌ 12 OTTOBRE 2018</strong> (email: tempiodellagrandemadre@gmail.com)</p>\n<p>Metodo di pagamento: caparra non rimborsabile di € 100 entro il termine iscrizioni, saldo entro il 23 dicembre 2018.<br>\nGli estremi per la caparra sono comunicati privatamente, alla compilazione del modulo di iscrizione.</p>\n<p><strong>Abbandonare il per.corso:</strong><br>\nOccasionalmente accade che un/a partecipante decida di lasciare il per.corso per motivi personali.\nColoro che lasciano il per.corso entro il terzo incontro (escluso) riceveranno un rimborso così calcolato: tariffa intera a cui verrà sottratta la caparra e il costo degli incontri effettuati (40 euro per incontro).</p>\n<p>ATTENZIONE: Non ci sono rimborsi per gli abbandoni dal terzo incontro.</p>\n<p>Se il per.corso è cancellato da parte dell’organizzazione prima del suo inizio, la caparra è sempre rimborsata. Se l’organizzazione è costretta a sospendere il per.corso dopo il suo inizio, sarà rimborsato l’intero costo meno gli incontri già svolti e il costo della tessera associativa.<br>\nNon saranno rimborsati i costi (quantificati in 40 euro) degli incontri effettivamente effettuati, anche in caso di assenza del partecipante.</p>\n<br/>\n<p><strong>Al termine del per.corso</strong> ogni partecipante potrà decidere se fermarsi o proseguire nella spirale successiva.</p>\n<p><strong>Facilitatrice:</strong> Laura Ghianda, madre, Sacerdotessa di Avalon e Sacerdotessa della Dea formata presso il Tempio della Dea di Glastonbury, ricercatrice indipendente sui temi della Grande Dea e del sacro femminino. Artista poliedrica, è laureata in Scienze dell'Educazione ed è educatrice professionale. Co-fondatrice dell'Associazione di Promozione Sociale <em>Tempio della Grande Madre</em> e del cerchio di \"Avalon Italia\", è promottrice del progetto <em>Fiamma della Grande Madre delle Dolomiti</em>.</p>\n<p><strong>Nota:</strong> il percorso proposto non intende sostituirsi alla Prima Spirale della scuola del Tempio di Glastonbury, ne è una versione personalizzata e adattata al nostro contesto territoriale.</p>\n<br/>\n<p align=\"center\">\n  <img style='width:60%;' border=\"0\" alt=\"testo sopra\" src=\"/media/photos/labirinto.jpg\">\n</p>\n<br/>\n<p><strong>Quando:</strong></p>\n<ol>\n<li>Domenica 11 novembre 2018: Ognissanti, la Decadenza. Introduzione sugli aspetti generali della Ruota dell’Anno e aspetto archetipico della Vecchia o Crona, archetipo della Morte, Rigenerazione e Rinascita.</li>\n<li>Domenica 23 dicembre 2018: Solstizio d'Inverno. Dedicato alla Signora dell’elemento Aria e al nuovo ciclo solare.</li>\n<li>Domenica 3 febbraio 2019: Candelora, il Risveglio. Dedicato all’aspetto archetipico della Bambina/Fanciulla.</li>\n<li>Domenica 17 marzo 2019: Equinozio di Primavera. Dedicato alla Signora dell’elemento Fuoco e al nuovo inizio.</li>\n<li>Domenica 5 maggio 2019: Calendimaggio, la Fioritura. Dedicato all’aspetto archetipico dell’Amante e alla festa di Beltane.</li>\n<li>Domenica 23 giugno 2019: Solstizio d'Estate. Dedicato alla Signora dell’elemento Acqua e alla pienezza.</li>\n<li>Domenica 4 luglio 2019: L'abbondanza. Dedicato all’aspetto archetipico della Madre.</li>\n<li>Sabato/Domenica 15/16 settembre 2019: Equinozio d'Autunno. Dedicato alla Signora dell’elemento Terra e al Centro della Ruota, che rappresenta la totalità degli aspetti. Nella sera di sabato si svolgerà, per coloro che lo desiderano, una personale cerimonia di dedicazione alla Grande Madre.</li>\n</ol>\n<p>Orari: dalle 10.00 alle 18.00 circa, piccola pausa pranzo nel mezzo.<br>\nDate e orari possono subire alcune modifiche che verranno eventualmente comunicate.</p>\n<p>Il percorso partirà con un numero minimo di 5 iscritte/i. 20 posti disponibili.</p>\n<br/>\n<p><strong>Dove:</strong>\nGli incontri 1, 2, 3 e 4 presso associazione “I guardiani della Terra\", via Grazioli 63, Trento.<br>\nGli incontri 5, 6 e 7 saranno possibilmente svolti all’aperto, sul territorio, tra le provincie di Trento e Bolzano.<br>\nL’ultimo incontro e la cerimonia di dedicazione si svolgeranno presso luogo da confermare e prevedono il pernottamento (eventuale costo pernottamento e pasti a carico del partecipante).</p>\n<br/>\n<p align=\"center\">\n  <img style='width:60%;' border=\"0\" alt=\"testo sopra\" src=\"/media/photos/altare-aria.jpg\">\n</p>\n<br/>","frontmatter":{"path":"/corso-ruota-dell-anno","title":"Conoscere e vivere la Ruota dell'Anno"}}},"pathContext":{}}

